import Axios from 'axios';
import { message } from 'antd';

const http = Axios.create({
  baseURL: process.env.REACT_APP_API_SERVER,
  withCredentials: true,
});

http.interceptors.request.use(
  (config) => {
    if (localStorage.__web_token) {
      config.headers.Authorization = 'Bearer ' + localStorage.__web_token;
    }
    if (localStorage.i18nextLng) {
      config.headers['Accept-Language'] = localStorage.i18nextLng;
    }
    return config;
  },
  (err) => {
    return Promise.reject(err);
  }
);

http.interceptors.response.use(
  (res) => {
    if (res.data.message) {
      message.success(res.data.message);
    }
    return res;
  },
  (err) => {
		
		
    if (err.response.status === 401) {
      localStorage.removeItem('__web_token');
			
      if (
        window.location.pathname.indexOf('login') === -1 && 
					window.location.pathname.indexOf('register') === -1
			) {
        window.location.hash = '/login';
      }
    }

    if (err.response.data) {
      message.error(err.response.data.message);
      return Promise.reject(err);
    }
    return Promise.reject(err);
  }
);

export default http;
