import { useEffect, useState } from 'react';
import { PostApi, PostSaveApi, PostDelApi } from 'utils/Api';
import {
  Table,
  Button,
  Modal,
  Upload,
  message,
  Form,
  Input,
  Select,
} from 'antd';
import { PlusOutlined } from '@ant-design/icons';
const { Column } = Table;
const { Option } = Select;
const { TextArea } = Input;

const Post = () => {
  const [tableData, setTableData] = useState();
  const [visible, setVisible] = useState(false);
  const [imageUrl, setImageUrl] = useState(null);
  const [form] = Form.useForm();

  useEffect(() => {
    PostApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          _id: item._id.slice(0, 7),
          id: item._id,
          createdAt: item.createdAt.replace(/[TZ]/g, ' '),
          updatedAt: item.updatedAt.replace(/[TZ]/g, ' '),
        }))
      );
    });
  }, []);

  const edit = (record) => {
    form.setFieldsValue(record);
    setImageUrl(record.imageUrl);
    setVisible(true);
  };
  const del = (record) => {
    PostDelApi(record.id).then((res) => {
      const newTableData = tableData.filter((item) => item.id !== record.id);
      setTableData(newTableData);
    });
  };

  const onChange = (info) => {
    if (info.file.status === 'done') {
      message.success('文件上传成功');
      setImageUrl(info.file.response);
    } else if (info.file.status === 'error') {
      message.error('文件上传失败');
    }
  };

  const onFinish = (value) => {
    PostSaveApi({ ...value, imageUrl }).then((res) => {
      message.success('保存成功');
      onCancel();
      const swapData = tableData.filter((item) => item.id !== res.data._id);
      setTableData([
        ...swapData,
        {
          ...res.data,
          _id: res.data._id.slice(0, 7),
          id: res.data._id,
        },
      ]);
    });
  };

  const onCancel = () => {
    setVisible(false);
    setImageUrl(null);
    form.resetFields();
  };
  return (
    <>
      <Button
        type="primary"
        className="bg-blue-600"
        onClick={() => setVisible(true)}
      >
        发布公告
      </Button>
      <Modal
        title="发布公告"
        centered
        visible={visible}
        onCancel={onCancel}
        width={1000}
        keyboard={false}
        maskClosable={false}
        footer={[]}
      >
        <Form
          labelCol={{ span: 4 }}
          form={form}
          name="control-hooks"
          onFinish={onFinish}
        >
          <Form.Item name="id" style={{ display: 'none' }}>
            <Input />
          </Form.Item>
          <Form.Item name="name" label="标题" rules={[{ required: true }]}>
            <Input />
          </Form.Item>
          <Form.Item name="category" label="类别" rules={[{ required: true }]}>
            <Select placeholder="请选择公告类别" allowClear>
              <Option value={1}>新手入门</Option>
              <Option value={2}>每日早报</Option>
              <Option value={3}>会员答疑</Option>
              <Option value={4}>项目教程</Option>
            </Select>
          </Form.Item>
          <Form.Item name="detail" label="详情">
            <TextArea rows={4} placeholder="输入公告内容" />
          </Form.Item>
          <Form.Item label="缩略图">
            <Upload
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
              }}
              name="file"
              action={process.env.REACT_APP_UPLOAD_SERVER}
              headers={
                localStorage.getItem('__web_token')
                  ? { Authorization: `Bearer ${localStorage.__web_token}` }
                  : null
              }
              showUploadList={false}
              onChange={onChange}
            >
              {imageUrl ? (
                <img
                  src={process.env.REACT_APP_SERVER + imageUrl}
                  alt="avatar"
									style={{width: '18rem', height: '6rem'}}
                />
              ) : (
                <div
                  style={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    width: '6rem',
                    height: '6rem',
                  }}
                >
                  <PlusOutlined />
                </div>
              )}
            </Upload>
          </Form.Item>
          <Form.Item wrapperCol={{ span: 4, offset: 4 }}>
            <Button type="link" htmlType="submit">
              保存
            </Button>
          </Form.Item>
        </Form>
      </Modal>

      <Table dataSource={tableData} rowKey="id">
        <Column title="ID" dataIndex="_id" />
        <Column title="标题" dataIndex="name" />
        <Column
          title="类别"
          dataIndex="category"
          render={(_, record) => (
            <>
              {record.category === 1 ? <p>新手入门</p> : null}
              {record.category === 2 ? <p>每日早报</p> : null}
              {record.category === 3 ? <p>会员答疑</p> : null}
              {record.category === 4 ? <p>项目教程</p> : null}
            </>
          )}
        />
        <Column title="添加时间" dataIndex="createdAt" />
        <Column title="最后修改时间" dataIndex="updatedAt" />
        <Column
          title="操作"
          dataIndex=""
          render={(_, record) => (
            <div className="flex gap-1">
              <Button type="link" onClick={() => edit(record)}>
                修改
              </Button>
              <Button type="link" onClick={() => del(record)}>
                删除
              </Button>
            </div>
          )}
        />
      </Table>
    </>
  );
};

export default Post;
