import http from './http'


export const LoginApi = (model) => http.post('/login', model)
export const RegisterApi = (model) => http.post('/register', model)
export const CaptchaApi = () => http.get('/../common/captcha')

export const DashBoardApi = () => http.get('/info/dashboard')

export const UserListApi = () => http.get('/user/list')
export const UserBanApi = (id) => http.get('/user/' + id)
export const UserEditApi = (model) => http.post('/user', model)
export const UserDelApi = (id) => http.delete('/user/' + id)

export const RostateApi = () => http.get('/info/rotate')
export const RostateSaveApi = (model) => http.post('/info/rotate', model)
export const RostateDelApi = (id) => http.delete('/info/rotate/' + id)

export const PostApi = () => http.get('/info/post')
export const PostSaveApi = (model) => http.post('/info/post', model)
export const PostDelApi = (id) => http.delete('/info/post/' + id)

export const AboutApi = () => http.get('/info/about')
export const AboutSaveApi = (model) => http.post('/info/about', model)

export const CenterApi = () => http.get('/info/center')
export const CenterSaveApi = (model) => http.post('/info/center', model)

export const ShareApi = () => http.get('/info/share')
export const ShareSaveApi = (model) => http.post('/info/share', model)

export const ProductApi = () => http.get('/product')
export const ProductSaveApi = (model) => http.post('/product', model)
export const ProductDelApi = (id) => http.delete('/product/' + id)

export const RecordApi = () => http.get('/product/record')

export const FinancialApi = () => http.get('/price/financial')
